.form-style-9{
	max-width: 450px;
	background: #FAFAFA;
	padding: 30px;
	margin: 50px auto;
	box-shadow: 1px 1px 25px rgba(0, 0, 0, 0.35);
	border-radius: 10px;
	border: 6px solid #305A72;
}
.form-style-9 ul{
	padding:0;
	margin:0;
	list-style:none;
}
.form-style-9 ul li{
	display: block;
	margin-bottom: 10px;
	min-height: 35px;
}
.form-style-9 ul li  .field-style{
	box-sizing: border-box; 
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box; 
	padding: 8px;
	outline: none;
	border: 1px solid #B0CFE0;

}.form-style-9 ul li  .field-style:focus{
	box-shadow: 0 0 5px #B0CFE0;
	border:1px solid #B0CFE0;
}
.form-style-9 ul li .field-split{
	width: 49%;
}
.form-style-9 ul li .field-full{
	width: 100%;
}
.form-style-9 ul li input.align-left{
	float:left;
}
.form-style-9 ul li input.align-right{
	float:right;
}
.form-style-9 ul li textarea{
	width: 100%;
	height: 100px;
}
.form-style-9 ul li input[type="button"], 
.form-style-9 ul li input[type="submit"] {
	box-shadow: inset 0px 1px 0px 0px #3985B1;
	background-color: #216288;
	border: 1px solid #17445E;
	display: inline-block;
	cursor: pointer;
	color: #FFFFFF;
	padding: 8px 18px;
	text-decoration: none;
	font: 12px Arial, Helvetica, sans-serif;
}
.form-style-9 ul li input[type="button"]:hover, 
.form-style-9 ul li input[type="submit"]:hover {
	background: linear-gradient(to bottom, #2D77A2 5%, #337DA8 100%);
	background-color: #28739E;
}
.lp-slideshow-gallery,
.lp-slideshow-gallery * {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

.lp-slideshow-gallery {
  width: 50%;
  margin: 17px auto;
}

.lp-slideshow-gallery > .container {
  position: relative;
  margin: auto;
  width: 100%;
}

.lp-slideshow-gallery > .container > .slide {
  width: 100%;
  height: 100%;
  position: absolute;
  margin: auto;
  opacity: 0;
  transition: opacity 0.6s ease;
  -webkit-transition: opacity 0.6s ease;
  -moz-transition: opacity 0.6s ease;
  -o-transition: opacity 0.6s ease;
}

.lp-slideshow-gallery > .container > .slide.active {
  opacity: 1;
}

.lp-slideshow-gallery > .container > .slide > .image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.lp-slideshow-gallery > .container > .slide > .number-text {
  color: #fff;
  font-size: 1rem;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}

.lp-slideshow-gallery > .container > .slide > .caption-text {
  color: #fff;
  font-size: 1.3rem;
  padding: 8px 12px;
  position: absolute;
  bottom: 0px;
  width: 100%;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.2);
}

.lp-slideshow-gallery > .container > .prev,
.lp-slideshow-gallery > .container > .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -20px;
  padding: 16px;
  font-size: 18px;
  color: #fff;
  font-weight: bold;
  transition: background-color 0.6s ease;
  -webkit-transition: background-color 0.6s ease;
  -moz-transition: background-color 0.6s ease;
  -o-transition: background-color 0.6s ease;
}

.lp-slideshow-gallery:hover > .container > .prev,
.lp-slideshow-gallery:hover > .container > .next {
  background-color: rgba(0, 0, 0, 0.2);
}

.lp-slideshow-gallery:hover > .container > .prev:hover,
.lp-slideshow-gallery:hover > .container > .next:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.lp-slideshow-gallery > .container > .next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

.lp-slideshow-gallery > .container > .prev {
  left: 0;
  border-radius: 0 3px 3px 0;
}

.lp-slideshow-gallery > .container-bottom {
  margin-bottom: 1rem;
  width: 100%;
  display: flex;
  display: -webkit-flex;
}

.lp-slideshow-gallery > .container-bottom > .image {
  flex: 1 1;
  object-fit: cover;
  opacity: 0.5;
  transition: opacity 0.6s ease;
  -webkit-transition: opacity 0.6s ease;
  -moz-transition: opacity 0.6s ease;
  -o-transition: opacity 0.6s ease;
}

.lp-slideshow-gallery > .container-bottom > .image.active,
.lp-slideshow-gallery > .container-bottom > .image:hover {
  opacity: 1;
}
.lp-slideshow-gallery > .container-bottom > .image:not(.active):hover {
  cursor: pointer;
}

*{
    margin:0;
    padding:0;
}

[data-icon]:before{
  font-family:'zocial';
  content:attr(data-icon);
  -webkit-font-smoothing:antialiased;
}

.clear{ clear:both; }

#main{
    background-color: #FFFFFF;
    padding: 200px 0 100px;
    position: relative;
    text-align: center;
    z-index: 1;
}

footer{
    background-color:#2E3639;
    position:relative;
    z-index:1;
}

footer > ul{
  list-style:none outside none;
  margin:0 auto;
  max-width:1200px;
  overflow:hidden;
  padding:25px 0;
  position:relative;
  width:95%;
}

footer > ul li{
  float:left;
  padding:20px 15px;
  width:33.3%;
  box-sizing:border-box;
  -moz-box-sizing:border-box;
}

footer > ul li:first-child{
  padding-left:0;
}

footer > ul li:nth-child(3){padding-right:0;}

footer > ul li .icon{
  color:#999999;
  float:left;
  font-size:80px;
  line-height:80px;
}

footer > ul li .text{
  color:#848889;
  font-size:13px;
  line-height:20px;
  margin-left:105px;
  position:relative;
  text-align:justify;
}

footer .bar{
  background-color:#1E2629;
  padding: 20px 0;
 
}

footer .bar-wrap{
  
  font-size:12px;
  margin:0 auto;
  max-width:1200px;
  position:relative;
  width:95%;
}

.links{
  float:left;
  list-style:none outside none;
  position:relative;
}

.links li{
  float:left;
  margin-right:10px;
}

.links a{ color:#778888;}

.links a:hover{color:#FFFFFF;}

.social{
  position:absolute;
  right:0;
  top:0;
}

.social a{ color:#778888; margin-left:20px;}
.social a:hover{color:#ffffff;}
.social .icon{
  display:inline-block;
  font-size:30px;
  margin-right:5px;
  vertical-align:middle;
  transition: transform .3s linear;
}

.social a:hover .icon{
 transform:rotate(360deg);
}

.social .info{
  display:inline-block;
  vertical-align:middle;
}

.social .info .num{display:block;}
.copyright{
  color:#778888;
  margin-top:-10px;
  float:left;
}

/* Responsive */

@media screen and (max-width:1000px){
  .links, .social, .copyright{
      float:none;
      text-align:center;
  }
  
  .social{
      position:relative;
      margin:10px 0;
  }
  
  .links li{
      display:inline-block;
      float:none;
  }
  
  .bar{
      position:relative;
  }
  
  .bar-wrap{
      margin-bottom:0;
  }
}

@media screen and (max-width: 835px)  {
  footer > ul li {
      float:none;
      width:auto;
  }
}

@media screen and (max-width: 768px)  {
  .links li {
      margin-right:5px;
  }
}
